const BlogTopCard = ({ post, index }) => {
  const { id, title, cover, content, author, createdAt } = post;
  const theme = useTheme();
  const navigate = useNavigate();
  // const latestPostLarge = index === 0;
  // const latestPost = index === 1 || index === 2;
  const matchDownMd = useMediaQuery(theme.breakpoints.down('md'))


  const handleDetail = (id) => {
    navigate(`/pages/blogs/${id}`)
  }

  return (
    <Box
      onClick={() => handleDetail(id)}
      sx={{
        display: 'flex',
        flexDirection: matchDownMd ? 'column' : 'row',
        gap: 2,
        width: '100%',
        backgroundColor: theme.palette.mode === 'dark' ? 'grey.700' : 'grey.200'
      }}
    >
      <Box
        component="img"
        src={cover}
        sx={{
          height: 300,
          width: '100%',
          gridColumn: '1/6',
          borderRadius: 2,
          borderTopRightRadius: matchDownMd ? 2 : 0,
          borderBottomRightRadius: matchDownMd ? 2 : 0,
        }}
      />
      <Box
        sx={{
          display: 'flex',
          flexDirection: 'column',
          gridColumn: '6/9',
          gap: 2,
          p: 2
        }}
      >
        <Typography variant='h5' sx={{ color: theme.palette.text.default }}>
          {title.substr(0, 240)}{title.title > 240 ? '...' : ''}
        </Typography>
        <Typography gutterBottom variant="body1" sx={{ height: 66, color: 'text.secondary', display: 'block', overflow: 'hidden' }}>
          {content.substr(0, 360)}{content.length > 360 ? '...' : ''}
        </Typography>
        <Box sx={{ flexGrow: 1 }} />
        <Stack direction="row" spacing={2} mt={2} alignItems={`center`}>
          <Avatar src={author.avatarUrl} />
          <Typography gutterBottom variant="subtitle2" sx={{ color: 'text.secondary', display: 'block' }}>
            {author.name}
          </Typography>
          <Typography gutterBottom variant="subtitle2" sx={{ color: 'text.secondary', display: 'block' }}>
            {fDate(createdAt)}
          </Typography>
        </Stack>
      </Box>
    </Box>
  );
}

BlogTopCard.propTypes = {
  post: PropTypes.object.isRequired,
  index: PropTypes.number,
};

export default BlogTopCard; 